export default {
	props: {
		item: Object,
		index: Number
	},
	data() {
		return {
			isguanZhu: this.item.isguanZhu,
			InfoNumIndex: this.item.infoNum.index,
			dingNum: this.item.infoNum.dingNum,
			caiNum: this.item.infoNum.caiNum
		};
	},
	methods: {
		changeIsguanZhu() {
			if (!this.isguanZhu) {
				this.isguanZhu = true;
				uni.showToast({
					title: '关注成功'
				});
			} else {
				this.isguanZhu = false;
				uni.showToast({
					title: '取消关注'
				});
			}
		},
		dingCai(num) {
			if (this.InfoNumIndex == num) {
				uni.showToast({
					title:"您已经操作过了！"
				})
				return
			}

			this.InfoNumIndex = num;
			if (num == 1) {
				uni.showToast({
					title:"顶"
				})
				this.caiNum--;
				this.dingNum++;
			} else if (num == 2) {
				uni.showToast({
					title:"踩"
				})
				this.dingNum--;
				this.caiNum++;
			}
		}
	}
};
